
body {
    background-image: url("./images/workspace.jpg");
    text-align: center;
    background-size: cover;
    background-color: gray;
    margin: auto;
    color: black;
}
router-link:link{
    color:black;
}
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
