    margin-right: 75px;
    margin-top: 15px;
}
li {
    list-style: none;
}
.getout {
    margin-top: 11px;
    margin-left: 10px;
    margin-right: 10px;
}
.dropdown:hover .dropdown-menu {
    display: block;
}
.firstRow {
    height: 4%;
}
#app {
    margin: 0;
    padding: 0;
}



